import React,{Component} from 'react';
import { Grid, Textfield, Button, Card, Cell } from 'react-mdl';
class Login extends Component {
  render() {
    return(
      <div style={{alignContent:'center', display:'flex',paddingTop: '50px'}}>
        
        <Card shadow={6} style={{ height:'280px', margin: 'auto'}}>
        <h3>Login</h3>
        <Grid>
                <Textfield
                    onChange={() => {}}
                    label="Email"
                    floatingLabel
                    style={{width: '200px'}}
                    required/>
                <Textfield
                    onChange={() => {}}
                    label="Password"
                    floatingLabel
                    style={{width: '200px'}}
                    required/>
               
          </Grid>
          <Button style={{}} raised accent ripple>Login</Button>
          
          </Card>
      </div>
    )
  }
}
export default Login;